* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background: #ccebe9;
}

.bbody {
  margin: 4rem 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100%;
}

#search-bar {
  display: grid;
  margin-bottom: 2rem;
  padding: 0.5rem;
  border-radius: 9px;
  background: #4378ff;
  border: transparent;
}

#outer-box {
  border-radius: 40px;
  background: #ffffff;
  overflow: hidden;
  width: 93vh;
  display: grid;
  gap: 2rem;
  grid-auto-flow: column;
  padding: 1rem;
  padding-bottom: 1.5rem;
}

.inner-box {
  display: grid;
  gap: 1rem;
}

.inner-img img {
  height: 6.5rem;
  border-radius: 50%;
  border: 3px solid #4378ff;
}

#list {
  list-style: none;
  display: grid;
  grid-auto-flow: column;
}

/* --------------  repos ------------------ */

#repos {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem;
}

#repos-link {
  padding: 6px;
  cursor: pointer;
  background: white;
  color: #4378ff;
  border-radius: 8px;
  font-size: 1rem;
  border: 1px solid;
}

#repos-link:hover {
  background: #4378ff;
  color: white;
}

::placeholder {
  color: white;
}

@media screen and (max-width: 580px) {
  #outer-box {
    width: 73vh;
    gap: 1rem;
    grid-auto-flow: row;
  }
}

@media screen and (max-width: 450px) {
  #outer-box {
    width: 49vh;
  }

  #list {
    grid-auto-flow: initial;
    grid-template-columns: 1fr 1fr;
    row-gap: 1rem;
  }

  #repos {
    grid-template-columns: 1fr 1fr;
  }

  .inner-box {
    gap: 1.5rem;
  }
}
